import styled from 'styled-components';
import { useRecoilValue } from 'recoil';
import { getModeState } from '../recoil/selector';
import { Carousel, Container, Cover, Header, Mouse } from '../components';

const Main = () => {
  const mode = useRecoilValue(getModeState);

  return (
    <MainLayout>
      <Header />
      <Container id="top" direction="column" mode={mode}>
        <Cover state={mode} />
        <Mouse state={mode} />
      </Container>
      <Container direction="column" mode={mode}>
        about
      </Container>
      <Container direction="column" mode={mode}>
        <Carousel />
      </Container>
      <Container direction="column" mode={mode}>
        Contact
      </Container>
    </MainLayout>
  );
};

export default Main;

const MainLayout = styled.main`
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
`;
